<template>
  <!-- <img :src="videURL" alt="GIF" style="width: 500px; height: 220px" /> -->
  <video autoplay loop style="width: 500px; height: 220px; object-fit: cover">
    <source :src="videURL" type="video/mp4" />
  </video>
  <ul>
    <li><a href="#">Home</a></li>

    <!-- <li><a href="#">Blog</a></li>
    <li><a href="#">Portfolio</a></li>
    <li><a href="#">About</a></li>
    <li><a href="#">Contact</a></li> -->
    <div class="cursor"></div>
  </ul>
</template>

<script>
export default {
  data() {
    return {
      videURL: require("@/assets/Main/video2.mp4"),
    };
  },
  mounted() {
    const $cursor = document.querySelector(".cursor");
    document.addEventListener("mousemove", function (e) {
      $cursor.style.left = e.clientX + "px";
      $cursor.style.top = e.clientY + "px";
    });
  },
};
</script>

<style>
body {
  display: flex;
  background-image: url("@/assets/Main/sky.jpg");
  background-size: cover;
}
ul,
li {
  list-style: none;
}
ul {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  top: 20px;
  left: 20px;
}
li {
  font-size: 40px;
  margin: 20px 0;
}
li a {
  text-decoration: none;
  color: #fff;
}
li:nth-child(2) a {
  color: red;
}
li:nth-child(3) a {
  color: blue;
}
li:nth-child(4) a {
  color: green;
}
li:nth-child(5) a {
  color: pink;
}
.cursor {
  position: fixed;
  width: 20px;
  height: 20px;
  border-radius: 50%;

  background: white;
  transition: transform 0.2s;
  mix-blend-mode: difference;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
ul li:hover ~ .cursor {
  transform: scale(5);
}
</style>
